body {  
  background-image: url('/public/assets/body-background.9e7d96f6.png');
  background-size: cover;
  color: white;
}

.App {
  max-width: 75vw;
  margin: 10vh auto;
}

.avatar-container{
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  margin-right: 12px;
  margin-bottom: 12px;
}

.top-container{
  background-position: center top 8%;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 200px;
  color: white;  

}

@font-face {
  font-family: "Lexend";
  src: local("Lexend-VariableFont"),
        url("./fonts/Lexend-VariableFont_wght.ttf")
}

.grid-item {
  background: linear-gradient(127.09deg, rgba(6, 11, 40, 0.94) 19.41%, rgba(10, 14, 35, 0.49) 76.65%);
  border-radius: 15px;
}

.paper-background{
  background-image: linear-gradient(126.97deg, rgb(17, 20, 39) 28.26%, rgb(16, 21, 48) 91.2%);
  color: white
}

.parent-grid-container:hover{
  background-color: #123c575a
}


#rainy-background{
  background-image: linear-gradient(127.09deg, rgba(6, 11, 40, 0.94) 19.41%, rgba(10, 14, 35, 0.49) 76.65%),  
  url('/public/assets/Icons/Conditions/rainy-background.svg');
}

#clear-background{
  background-image: linear-gradient(127.09deg, rgba(6, 11, 40, 0.94) 19.41%, rgba(10, 14, 35, 0.49) 76.65%),  
  url('/public/assets/Icons/Conditions/clear-background.svg');
  background-size: cover;
  background-position: 3em -3em;

}

#snowy-background{
  background-image: linear-gradient(127.09deg, rgba(6, 11, 40, 0.94) 19.41%, rgba(10, 14, 35, 0.49) 76.65%),  
  url('/public/assets/Icons/Conditions/snowy-background.svg');
  background-size: cover;
 

}

#foggy-background{
  background-image: linear-gradient(127.09deg, rgba(6, 11, 40, 0.94) 19.41%, rgba(10, 14, 35, 0.49) 76.65%),  
  url('/public/assets/Icons/Conditions/foggy-background.svg');
  background-size: cover;
  background-position: center;
}

#cloudy-background{
  background-image: linear-gradient(127.09deg, rgba(6, 11, 40, 0.94) 19.41%, rgba(10, 14, 35, 0.49) 76.65%),  
  url('/public/assets/Icons/Conditions/cloudy-background.svg');
  background-size: cover;
  background-position: center;
}

#partly-cloudy-background{
  background: linear-gradient(127.09deg, rgba(36, 46, 101, 0.94) 19.41%, rgba(10, 14, 35, 0.49) 76.65%),
  url('/public/assets/Icons/Conditions/partly-cloudy-background.svg');
  background-size: cover;
  background-position: center;
}

#partly-cloudy-night-background{
  background: 
  linear-gradient(127.09deg, rgba(1, 2, 9, 0.24) 100%, rgba(0, 0, 3, 0.9) 76.65%),
  url('/public/assets/Icons/Conditions/partly-cloudy-night-background.svg');
  background-size: cover;
  background-position: center;
}

#sunny-background{
  background-image: 
  
  radial-gradient(circle, rgb(239, 239, 238) 10%, rgba(88,230,255,0.8435749299719888) 50%, rgba(0, 0, 0, 0.1) 120%),  
  url('/public/assets/Icons/Conditions/sunny-background.svg');
  background-size: 110%;
  
  background-position: center;
}
